 <!--商品分类列表-->
<template>
  <div class="cms-list-layout">
    <cms-prompt>
      <li>设置商城所有使用的区域信息，请谨慎操作</li>
    </cms-prompt>
    <section class="cms-batch" style="border:0;">
      <cms-button v-prem="'/region/save'" icon="add" type="success" size="small" @click="$routerLink('/region/save','save',{parentIds:'0',areaType:queryParams.areaType})">新增</cms-button>
    </section>

    <el-table :data="tableData" :row-style="showRow" style="width:100%" v-loading='loading'>
      <el-table-column label="区域名称" min-width="200">
        <template slot-scope="scope">
          <div class="menu-name" :style="{marginLeft:(scope.row._level*16)+'px'}" v-if="iconShow(scope.$index,scope.row)" @click="toggleExpanded(scope.$index)">
            <span class="tree-ctrl">
              <i v-if="scope.row._expanded" class="el-icon-caret-right"></i>
              <i v-else class="el-icon-caret-bottom"></i>
            </span>
            <span>{{scope.row.areaName}}</span>
          </div>
          <div v-else :style="{marginLeft:(scope.row._level*16)+'px'}">
            <span class="icon-dot">○</span>
            <span>{{scope.row.areaName}}</span>
          </div>
        </template>
      </el-table-column>
      <el-table-column label="区域类型" prop="areaTypeName">
        <template slot-scope="scope">
          {{getAreaType(scope.row.areaDictCode)}}
        </template>
      </el-table-column>
      <el-table-column label="排序" prop="sortNum" align="center">
        <template slot-scope="scope">
          <el-input maxlength="2" v-model="scope.row.sortNum" @keyup.enter.native="sortNum($event,scope.row.id)"> </el-input>
        </template>
      </el-table-column>
      <el-table-column label="创建时间" prop="" min-width="180">
        <template slot-scope="scope">
          {{$moment(scope.row.createTime)}}
        </template>
      </el-table-column>

      <el-table-column label="操作" width='280' fixed="right" align="center">
        <template slot-scope="scope">
          <cms-button icon='add' size='small' type='info' v-if="scope.row.menuType===1" @click="$routerLink('/region/save','save',{nodeIds:scope.row.parentIds.toString(),areaType:queryParams.areaType})">添加下层菜单</cms-button>
          <cms-button v-prem="'/region/edit'" icon='edit' size='small' type='info' @click="$routerLink('/region/edit','update',{id:scope.row.id, areaType:queryParams.areaType})" >编辑</cms-button>
          <cms-button v-prem="'/region/delete'" icon='delete' size='small' type='info' @click.stop="deleteData(scope.row,scope.$index)">删除</cms-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
import treeToArray from '@/utils/eval.js'
import { areaRegionList, areaRegionDelete, areaRegionSort } from '@/api/setting/area.js'
import list from '@/mixins/list'
export default {
  name: 'region',
  mixins: [list],
  data() {
    return {
      typeList: [],
      tableData: [],
      expandAll: false,
      queryParams: {
        parentId: '',
        areaType: '1',
        code: '',
        name: ''
      },
      selfId: 0, // 当前点击的id 不可删除
      columnState: true // 判断行隐藏
    }
  },
  computed: {
    dictData() {
      return this.$store.state.dict.data
    }
  },
  methods: {
    getAreaType(code) {
      const data = this.dictData.filter(v => v.dictType === 'area_type')
      let name = ''
      if (data.length > 0) {
        data[0].datas.forEach(v => {
          if (v.dictCode === code) {
            name = v.dictLabel
            return name
          }
        })
      }
      return name
    },
    sortNum(el, id) {
      // 数字排序
      const val = el.target.value
      const reg = /^[1-9][0-9]{0,1}$/
      if (val === '') {
        return false
      } else {
        if (reg.test(val)) {
          el.target.className = 'el-input__inner'
          areaRegionSort({ sorts: [{
            id: id,
            sortNum: val
          }] }).then(res => {
            if (res.code === 200) {
              this.$successMessage('修改成功')
            } else {
              this.$errorMessage('修改失败')
            }
          })
        } else {
          el.target.className = 'cms-error el-input__inner'
          this.$cmsMessage({
            message: '只能输入1-99的正整数',
            type: 'error',
            timer: '1000',
            fastClick: false
          })
        }
      }
    },
    showRow: function(row) {
      const show = row.row._show
      row.row._show = show
      return show
        ? 'animation:treeTableShow 1s;-webkit-animation:treeTableShow 1s;'
        : 'display:none;'
    },
    // 切换下级是否展开
    toggleExpanded: function(trIndex) {
      const record = this.tableData[trIndex]
      if (record._expanded) {
        // 判断展开
        if (!record._load) {
          // 判断是否加载过
          this.queryParams.parentId = record.id
          this.loading = true
          areaRegionList(this.queryParams).then(res => {
            if (res.code === 200) {
              const arr = treeToArray(res.data)
              for (let i = 0; i < arr.length; i++) {
                this.tableData.splice(trIndex + (i + 1), 0, arr[i])
              }
              record._expanded = false
              record._load = true
              this.loading = false
            }
          })
        } else {
          this.chidView(record, true)
          record._expanded = false
        }
      } else {
        record._expanded = true
        this.chidView(record, false)
      }
    },
    chidView(record, state) {
      // 子集显示，核心判断这个方法
      const arr = []
      Array.from(this.tableData).forEach(v => {
        if (v.nodeIds.indexOf(record.id) > -1 && v.id !== record.id) {
          v._show = state
        }
        arr.push(v)
      })
      this.tableData = arr
    },
    // 图标显示
    iconShow(index, record) {
      return !record.isChild
    },
    getTableData(callBack, params) {
      callBack(params).then(res => {
        if (res.code === 200) {
          this.tableData = treeToArray(res.data)
          this.loading = false
        }
      })
    },
    deleteData(row, index) {
      // 删除区域
      this.$confirm('是否确定删除区域？', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'error'
      })
        .then(() => {
          this.loading = true
          areaRegionDelete({ ids: [row.id] }).then(res => {
            this.loading = false
            if (res.code === 200) {
              if (row.isChild) {
                this.tableData.splice(index, 1)
              } else {
                this.queryParams.parentId = ''
                this.getTableData(areaRegionList, this.queryParams)
              }
              this.$successMessage('删除成功')
            } else {
              this.$errorMessage('删除失败,' + res.message)
            }
          })
        })
        .catch(() => {})
    },
    getTypeList() {
      this.getTableData(areaRegionList, this.queryParams)
    }
  },
  created() {
    this.getTypeList()
  }
}
</script>
<style rel="stylesheet/css">

</style>
<style lang="scss" rel="stylesheet/scss" scoped>
$color-blue: #188ae2;

.menu-name {
  position: relative;
  cursor: pointer;
  user-select: none;
}
.tree-ctrl {
  cursor: pointer;
  color: $color-blue;
  margin-left: 0px;
}
.icon-dot {
  width: 14px;
  height: 14px;
  display: inline-block;
  position: relative;
  top: 1px;
  left: 4px;
  color: $color-blue;
  font-size: 18px;
  font-weight: bold;
}
</style>



